import React, { useState } from 'react';
import './LearningPage.css';

const LearningPage = () => {
  const [skillDescription, setSkillDescription] = useState('');
  const [learningTopics, setLearningTopics] = useState([]);
  const [loading, setLoading] = useState(false);

  const fetchLearningGuidance = async () => {
    if (!skillDescription.trim()) {
      alert('Please enter a description, keywords, or sentences about what you want to learn.');
      return;
    }

    setLoading(true);
    try {
      const response = await fetch('https://guanzydev.pythonanywhere.com/api/learn-skills', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ skill_description: skillDescription }),
      });

      if (!response.ok) {
        throw new Error(`API Error: ${response.status}`);
      }

      const data = await response.json();
      setLearningTopics(data.learning_guidance.topics || []);
    } catch (error) {
      console.error('Error fetching learning guidance:', error);
      alert('Failed to fetch learning guidance. Please try again.');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="learning-page">
      <h1>Learning Hub</h1>
      <div className="input-section">
        <textarea
          placeholder="Describe what you want to learn (e.g., AWS, Python programming, or cloud infrastructure)..."
          value={skillDescription}
          onChange={(e) => setSkillDescription(e.target.value)}
        ></textarea>
        <button onClick={fetchLearningGuidance} disabled={loading}>
          {loading ? 'Loading...' : 'Generate Knowledge Cards'}
        </button>
      </div>

      {learningTopics.length > 0 && (
        <div className="topics-section">
          <h2>Learning Topics</h2>
          {learningTopics.map((topic, index) => (
            <div key={index} className="topic-card">
              <h3>{topic.Concept}</h3>
              <p>
                <strong>Description:</strong> {topic.Description}
              </p>
              <p>
                <strong>Example:</strong> {topic.Example}
              </p>
              <p>
                <strong>Guidance:</strong> {topic.Guidance}
              </p>
            </div>
          ))}
        </div>
      )}

      {learningTopics.length === 0 && !loading && (
        <p className="no-topics-message">Enter a skill description to start generating knowledge cards.</p>
      )}
    </div>
  );
};

export default LearningPage;
