import React from "react";
import { NavLink } from "react-router-dom";
import { useAuth } from "../../context/AuthContext";
import "./Header.css";

const Header = () => {
  const { currentUser, logout } = useAuth();

  return (
    <header className="header">
      {/* Logo */}
      <NavLink to="/" className="logo">
        SkillPath
      </NavLink>

      {/* Navigation Links */}
      <nav className="nav-links">
        <NavLink to="/">Home</NavLink>
        <NavLink to="/mock-interviews">Mock Interviews</NavLink>
        <NavLink to="/resume-tools">Resume Tools</NavLink>
        <NavLink to="/knowledge-hub">Knowledge Hub</NavLink>
      </nav>

      {/* Auth Buttons or Welcome Message */}
      <div className="auth-buttons">
        {currentUser ? (
          <span className="welcome-message">
            Welcome, {currentUser.email}{" "}
            <span className="logout-link" onClick={logout}>
              Logout
            </span>
          </span>
        ) : (
          <>
            <NavLink to="/login" className="login-btn">
              Login
            </NavLink>
            <NavLink to="/signup" className="signup-btn">
              Sign Up
            </NavLink>
          </>
        )}
      </div>
    </header>
  );
};

export default Header;
