import React, { useState } from 'react';
import './ResumeToolsPage.css';

const ResumeToolsPage = () => {
  const [resume, setResume] = useState('');
  const [existingKeywords, setExistingKeywords] = useState([]);
  const [missingKeywords, setMissingKeywords] = useState([]);
  const [sections, setSections] = useState({});
  const [userInputs, setUserInputs] = useState({});
  const [updatedResume, setUpdatedResume] = useState('');

  const processResume = async () => {
    try {
      const response = await fetch('https://guanzydev.pythonanywhere.com/api/process-resume', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ resume_text: resume }),
      });

      if (!response.ok) {
        throw new Error(`API Error: ${response.status}`);
      }

      const data = await response.json();
      const keywords = data.keywords;

      const sectionsData = {};
      const existing = [];
      const missing = [];

      keywords.forEach((item) => {
        sectionsData[item.Section] = {
          existing: item.Existing_Keywords,
          missing: item.Missing_Keywords,
        };
        existing.push(item.Existing_Keywords);
        missing.push(item.Missing_Keywords);
      });

      setSections(sectionsData);
      setExistingKeywords(existing);
      setMissingKeywords(missing);
    } catch (error) {
      console.error('Error processing resume:', error);
      alert('Failed to analyze the resume. Please try again.');
    }
  };

  const improveResume = async () => {
    try {
      const response = await fetch('https://guanzydev.pythonanywhere.com/api/improve-resume', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          current_resume: resume,
          existing_keywords: existingKeywords.join(', '),
          missing_keywords: missingKeywords.join(', '),
          new_input: Object.values(userInputs).join('. '),
        }),
      });

      if (!response.ok) {
        throw new Error(`API Error: ${response.status}`);
      }

      const data = await response.json();
      setUpdatedResume(data.improved_resume);
      setResume(data.improved_resume);
      alert('Resume updated successfully!');
    } catch (error) {
      console.error('Error improving resume:', error);
      alert('Failed to update the resume. Please try again.');
    }
  };

  const handleInputChange = (section, value) => {
    setUserInputs((prevInputs) => ({
      ...prevInputs,
      [section]: value,
    }));
  };

  return (
    <div className="resume-tools-container">
      <h1>Resume Tools</h1>
      <div className="top-section">
        <textarea
          placeholder="Paste your resume in text format here..."
          value={resume}
          onChange={(e) => setResume(e.target.value)}
        ></textarea>
        <button onClick={processResume}>Analyze</button>
      </div>

      {Object.keys(sections).length > 0 && (
        <div className="analysis-section">
          <h2>Resume Analysis</h2>
          {Object.entries(sections).map(([section, { existing, missing }]) => (
            <div key={section} className="section-analysis">
              <h3>{section}</h3>
              <p>
                <strong>Existing Keywords:</strong> {existing}
              </p>
              <p>
                <strong>Missing Keywords:</strong> {missing}
              </p>
              <textarea
                placeholder={`Describe more details for ${section}...`}
                value={userInputs[section] || ''}
                onChange={(e) => handleInputChange(section, e.target.value)}
              ></textarea>
            </div>
          ))}
          <button onClick={improveResume}>Submit All</button>
        </div>
      )}

      {updatedResume && (
        <div className="updated-resume-section">
          <h2>Updated Resume</h2>
          <textarea value={updatedResume} readOnly></textarea>
        </div>
      )}
    </div>
  );
};

export default ResumeToolsPage;
