
import React from 'react';
import './LandingPage.css';

const LandingPage = () => {
  return (
    <div className="landing-page">
      <section className="intro">
        <h1>Welcome to SkillPath</h1>
        <p>Your one-stop solution for Mock Interviews, Resume Building, and Personalized Learning.</p>
      </section>
      <section className="features">
        <div className="column">
          <h2>Mock Interviews</h2>
          <p>Practice tailored interviews with realistic questions and AI-powered feedback.</p>
          <div className="workflow">
            <ul>
              <li>Step 1: Select an interview type.</li>
              <li>Step 2: Provide relevant job details.</li>
              <li>Step 3: Practice with personalized questions.</li>
              <li>Step 4: Receive actionable feedback.</li>
            </ul>
          </div>
        </div>
        <div className="column">
          <h2>Resume Tools</h2>
          <p>Optimize your resume with keyword analysis and actionable suggestions.</p>
          <div className="workflow">
            <ul>
              <li>Step 1: Paste your current resume.</li>
              <li>Step 2: Highlight existing and missing keywords.</li>
              <li>Step 3: Add additional details.</li>
              <li>Step 4: Generate an improved resume.</li>
            </ul>
          </div>
        </div>
        <div className="column">
          <h2>Learning Hub</h2>
          <p>Access curated learning resources to build skills and advance your career.</p>
          <div className="workflow">
            <ul>
              <li>Step 1: Describe the skills you want to learn.</li>
              <li>Step 2: View personalized knowledge cards.</li>
              <li>Step 3: Explore curated resources.</li>
              <li>Step 4: Track your progress.</li>
            </ul>
          </div>
        </div>
      </section>
    </div>
  );
};

export default LandingPage;
