import React, { useState } from "react";
import "./MockInterviewPage.css";
import { Chart as ChartJS, RadialLinearScale, PointElement, LineElement, Filler, Tooltip, Legend } from "chart.js";
import { Radar } from "react-chartjs-2";
import ButtonWithSpinner from "../../components/ButtonWithSpinner/ButtonWithSpinner";

// Register required components
ChartJS.register(RadialLinearScale, PointElement, LineElement, Filler, Tooltip, Legend);


const MockInterviewPage = () => {
  const [resume, setResume] = useState("");
  const [jobDescription, setJobDescription] = useState("");
  const [selectedJob, setSelectedJob] = useState("");
  const [numBehaviorQuestions, setNumBehaviorQuestions] = useState(3);
  const [numTechnicalQuestions, setNumTechnicalQuestions] = useState(3);
  const [numCaseQuestions, setNumCaseQuestions] = useState(0);
  const [maxFollowUps, setMaxFollowUps] = useState(2);
  const [questions, setQuestions] = useState([]);
  const [caseNarrative, setCaseNarrative] = useState("");
  const [caseQuestionIndices, setCaseQuestionIndices] = useState(new Set());
  const [currentQuestionIndex, setCurrentQuestionIndex] = useState(0);
  const [answers, setAnswers] = useState({});
  const [followUps, setFollowUps] = useState([]);
  const [evaluations, setEvaluations] = useState([]);
  const [isInterviewStarted, setIsInterviewStarted] = useState(false);
  const [isInterviewEnded, setIsInterviewEnded] = useState(false);
  const [isLoading, setIsLoading] = useState(false);

  const presetJobs = ["Software Engineer", "Product Manager", "Data Scientist"];

  // Base API URL from environment variable
  const BASE_URL = process.env.REACT_APP_BASE_URL;

  const fetchQuestions = async () => {
    setIsLoading(true);
    try {
      const responses = await Promise.all([
        numBehaviorQuestions > 0
          ? fetch(`${BASE_URL}${process.env.REACT_APP_BEHAVIOR_QUESTIONS_ENDPOINT}`, {
            method: "POST",
            headers: { "Content-Type": "application/json" },
            body: JSON.stringify({
              resume,
              job_description: jobDescription,
              num_questions: numBehaviorQuestions,
            }),
          }).then((res) => res.json())
          : Promise.resolve([]),

        numTechnicalQuestions > 0
          ? fetch(`${BASE_URL}${process.env.REACT_APP_TECH_QUESTIONS_ENDPOINT}`, {
            method: "POST",
            headers: { "Content-Type": "application/json" },
            body: JSON.stringify({
              job_description: jobDescription,
              num_questions: numTechnicalQuestions,
            }),
          }).then((res) => res.json())
          : Promise.resolve([]),

        numCaseQuestions > 0
          ? fetch(`${BASE_URL}${process.env.REACT_APP_CASE_QUESTIONS_ENDPOINT}`, {
            method: "POST",
            headers: { "Content-Type": "application/json" },
            body: JSON.stringify({
              job_description: jobDescription,
              num_questions: numCaseQuestions,
            }),
          }).then((res) => res.json())
          : Promise.resolve([]),
      ]);

      const behaviorQuestions = responses[0]?.questions || [];
      const technicalQuestions = responses[1]?.questions || [];

      let caseQuestions = [];
      let caseNarrativeText = "";
      let caseIndices = new Set();

      if (responses[2]?.questions) {
        caseNarrativeText = responses[2].questions.case_narrative || "";
        caseQuestions = responses[2].questions.questions || [];
        const startIdx = behaviorQuestions.length + technicalQuestions.length;
        caseIndices = new Set(caseQuestions.map((_, i) => startIdx + i));
      }

      // Store the narrative separately and update state
      setCaseNarrative(caseNarrativeText);
      setQuestions([...behaviorQuestions, ...technicalQuestions, ...caseQuestions]);
      setIsInterviewStarted(true);
      setCaseQuestionIndices(caseIndices);

    } catch (error) {
      console.error("Error fetching questions:", error);
      alert("Failed to generate questions. Please try again.");
    } finally {
      setIsLoading(false);
    }
  };

  const handleSubmitAnswer = async () => {
    const currentAnswer = answers[currentQuestionIndex];
    if (!currentAnswer) return;
    setIsLoading(true);

    const concatenatedQA = [
      { question: questions[currentQuestionIndex], answer: currentAnswer },
      ...followUps.map((f) => ({ question: f.question, answer: f.answer })),
    ]
      .map((qa) => `question: ${qa.question}\nanswer: ${qa.answer}`)
      .join("\n\n");

    try {
      const response = await fetch(`${BASE_URL}${process.env.REACT_APP_FOLLOWUP_ENDPOINT}`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          question_and_answer: concatenatedQA,
        }),
      });

      const data = await response.json();
      if (data.questions.if_followup_question) {
        if (followUps.length < Math.min(maxFollowUps, 5)) {
          setFollowUps((prev) => [
            ...prev.map((f) => ({
              ...f,
              hideSubmit: true,
              readOnly: true,
            })),
            {
              id: prev.length + 1,
              question: data.questions.followup_question,
              answer: "",
              hideSubmit: false,
              readOnly: false,
            },
          ]);
        } else {
          await evaluateCombinedQA(concatenatedQA);
          moveToNextQuestion();
        }
      } else {
        await evaluateCombinedQA(concatenatedQA);
        moveToNextQuestion();
      }
    } catch (error) {
      console.error("Error submitting answer:", error);
      alert("Failed to submit your answer. Please try again.");
    } finally {
      setIsLoading(false);
    }
  };

  const evaluateCombinedQA = async (concatenatedQA) => {
    try {
      const response = await fetch(`${BASE_URL}${process.env.REACT_APP_EVALUATE_ENDPOINT}`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          question_and_answers: concatenatedQA,
        }),
      });

      const data = await response.json();
      if (response.ok) {
        setEvaluations((prev) => [
          ...prev,
          {
            questionAndAnswers: concatenatedQA,
            evaluation: data.evaluation,
          },
        ]);
      } else {
        console.error("Error from evaluation API", data);
      }
    } catch (error) {
      console.error("Error evaluating combined QA:", error);
      alert("Failed to evaluate the response. Please try again.");
    }
  };

  const moveToNextQuestion = () => {
    if (currentQuestionIndex + 1 < questions.length) {
      setCurrentQuestionIndex((prev) => prev + 1);
      setFollowUps([]);
    } else {
      setIsInterviewEnded(true);
    }
  };

  const jobDescriptions = {
    "Software Engineer": "Designs, develops, and maintains software applications, ensuring code quality, scalability, and performance. Collaborates with cross-functional teams to implement new features, troubleshoot issues, and optimize existing systems. Works with various programming languages, frameworks, and tools to build efficient, user-friendly solutions.",
    "Product Manager": "Defines product vision, strategy, and roadmap based on market research and customer needs. Works closely with engineering, design, and business teams to develop and launch innovative products. Prioritizes features, manages stakeholder expectations, and ensures alignment between technical feasibility and business goals.",
    "Data Scientist": "Collects, cleans, and analyzes large datasets to uncover insights and drive data-informed decisions. Develops machine learning models and statistical algorithms to solve business problems. Works with engineering teams to deploy models, optimize data pipelines, and ensure continuous improvement of predictive analytics."
  };


  const RadarChartComponent = ({ evaluation }) => (
    <Radar
      data={{
        labels: Object.keys(evaluation).filter((key) => !key.includes("Rationale")),
        datasets: [
          {
            label: "Evaluation Scores",
            data: Object.keys(evaluation)
              .filter((key) => !key.includes("Rationale"))
              .map((key) => evaluation[key]),
            backgroundColor: "rgba(0, 123, 255, 0.2)",
            borderColor: "#007bff",
            borderWidth: 1,
          },
        ],
      }}
      options={{
        scales: {
          r: {
            suggestedMin: 0,
            suggestedMax: 5,
          },
        },
      }}
    />
  );

  return (
    <div className="mock-interview-page">
      <h1 className="page-title">Mock Interview</h1>
      {/* Setup Section */}
      {!isInterviewStarted && (
        <div className="setup-section">
          <textarea
            className="input-area"
            placeholder="Paste your resume (optional)..."
            value={resume}
            onChange={(e) => setResume(e.target.value)}
          ></textarea>
          <textarea
            className="input-area"
            placeholder="Paste job description here..."
            value={jobDescription}
            onChange={(e) => setJobDescription(e.target.value)}
          ></textarea>
          <select
            className="dropdown"
            value={selectedJob}
            onChange={(e) => {
              const selected = e.target.value;
              setSelectedJob(selected);
              setJobDescription(jobDescriptions[selected] || "");
            }}
          >
            <option value="">Preset Sample Job Description</option>
            {presetJobs.map((job) => (
              <option key={job} value={job}>
                {job}
              </option>
            ))}
          </select>
          <div className="settings">
            <label style={{ display: "block" }}>
              Number of Behavioral Questions:
            </label>

            <input
              className="number-input"
              type="number"
              min="0"
              value={numBehaviorQuestions}
              onChange={(e) => setNumBehaviorQuestions(parseInt(e.target.value))}
            />
            <label style={{ display: "block" }}>
              Number of Technical Questions:
            </label>

            <input
              className="number-input"
              type="number"
              min="0"
              value={numTechnicalQuestions}
              onChange={(e) => setNumTechnicalQuestions(parseInt(e.target.value))}
            />
            <label style={{ display: "block" }}>
              Number of Case Questions:
            </label>

            <input
              className="number-input"
              type="number"
              min="0"
              value={numCaseQuestions}
              onChange={(e) => setNumCaseQuestions(parseInt(e.target.value))}
            />
            <label style={{ display: "block" }}>
              Max Follow-Up Questions:
            </label>

            <input
              className="number-input"
              type="number"
              min="0"
              max="5"
              value={maxFollowUps}
              onChange={(e) => setMaxFollowUps(Math.min(5, parseInt(e.target.value)))}
            />
          </div>
          <ButtonWithSpinner isLoading={isLoading} onClick={fetchQuestions}>
            {isLoading ? "Loading..." : "Start Interview"}
          </ButtonWithSpinner>
        </div>
      )}

      {isInterviewStarted && !isInterviewEnded && (
        <div className="interview-section">
          <h3>
            Question {currentQuestionIndex + 1} of {questions.length}
          </h3>

          {/* Show Case Narrative ONLY for Case Questions */}
          {caseNarrative && caseQuestionIndices.has(currentQuestionIndex) && (
            <div className="case-narrative-container">
              <h4>Case Narrative</h4>
              <p>{caseNarrative}</p>
            </div>
          )}

          {/* Main Question Section */}
          <div className="question-answer-section">
            <p>{questions[currentQuestionIndex]}</p>
            <textarea
              className="answer-area"
              value={answers[currentQuestionIndex] || ""}
              onChange={(e) =>
                setAnswers((prev) => ({
                  ...prev,
                  [currentQuestionIndex]: e.target.value,
                }))
              }
              readOnly={followUps.length > 0} // Disable editing if follow-ups are present
            ></textarea>
            {!followUps.length && (
              <ButtonWithSpinner isLoading={isLoading} onClick={handleSubmitAnswer}>
                {isLoading ? "Loading..." : "Submit"}
              </ButtonWithSpinner>
            )}
          </div>

          {/* Follow-Up Question Sections */}
          {followUps.map((followUp, idx) => (
            <div className="follow-up-container" key={idx}>
              <p className="question-text">{followUp.question}</p>
              <textarea
                className="answer-area"
                value={followUp.answer || ""}
                onChange={(e) =>
                  setFollowUps((prev) => {
                    const updated = [...prev];
                    updated[idx] = { ...updated[idx], answer: e.target.value };
                    return updated;
                  })
                }
                readOnly={followUp.readOnly} // Disable editing for older answers
              ></textarea>
              {!followUp.hideSubmit && (
                <ButtonWithSpinner isLoading={isLoading} onClick={handleSubmitAnswer}>
                  {isLoading ? "Loading..." : "Submit"}
                </ButtonWithSpinner>
              )}
            </div>
          ))}
        </div>
      )}


      {isInterviewEnded && (
        <div className="evaluation-section">
          <h2 className="section-title">Interview Complete</h2>
          <div className="evaluation-cards-container">
            {evaluations.map((evaluation, idx) => (
              <div key={idx} className="evaluation-card">
                <h3 className="evaluation-question">Question {idx + 1}</h3>
                <pre className="evaluation-combined-qa">{evaluation.questionAndAnswers}</pre>
                <div className="evaluation-scores">
                  {Object.entries(evaluation.evaluation).map(([key, value]) => {
                    if (key.includes("Rationale")) return null;
                    return (
                      <div key={key} className="evaluation-score">
                        <h4 className="score-title">
                          {key.replace(/_Score/g, "")}: {value}/5
                        </h4>
                        <p className="score-rationale">
                          {evaluation.evaluation[`${key}_Rationale`] || "No rationale provided."}
                        </p>
                      </div>
                    );
                  })}
                </div>
                <div className="radar-plot">
                  <RadarChartComponent evaluation={evaluation.evaluation} />
                </div>
              </div>
            ))}
          </div>
        </div>
      )}


    </div>
  );
};

export default MockInterviewPage;
